export default function Homepage() {
    return (
        <article className="page">
            <AboutSection />
            <Navigation />
        </article>
    );
}

const Navigation = () => {
    return (
        <nav>
            <Link title="media" url="media"/>
            <Link title="wiki" url="wiki"/>
            <Link title="download" url="download"/>
        </nav>
    );
};

const Link = ({ title, url}) => {
    return (
        <a className="link" href={url}>
            <div className="link-title">
                <h3 class="unselectable">{title}</h3>
            </div>
        </a>
    );
};

const AboutSection = () => {
    return (
        <section>
            <h2 class="unselectable">about mAchina</h2>
            <p>
                MACHINA is a tech / exploration mod for Minecraft! Explore infinite procedurally generated planets, build huge factories, and create your own adventure.
            </p><br/>
            <p>
                The mod is currently in early development by <code>@Cy4#2695</code>, with additional assistance from <code>@Cy4#2695</code> and <code>@Cy4#2695</code>.
            </p><br/>
            <p>
                MACHINA is{' '}<a href="https://github.com/Cy4Shot/MACHINA">Open Source</a>!
                Follow development or discuss MACHINA on our {' '}<a href="https://discord.gg/PAGT2fu">Discord</a>.
            </p>
        </section>
    );
};